<style scoped>

</style>
<template>
    <div>
        <Breadcrumb>
            <BreadcrumbItem to="/team/reservation">所有场馆</BreadcrumbItem>
            <BreadcrumbItem>我的预约</BreadcrumbItem>
        </Breadcrumb>
        <Divider></Divider>
        <Form inline :model="formData" :label-width="80">

            <!--<FormItem label="状态">-->
            <!--<Select class="filter-input" v-model="formData.status" placeholder="请选择">-->
            <!--<Option-->
            <!--label="正常"-->
            <!--:value="1">-->
            <!--</Option>-->
            <!--<Option-->
            <!--label="禁用"-->
            <!--:value="0">-->
            <!--</Option>-->
            <!--</Select>-->
            <!--</FormItem>-->
            <FormItem label="日期">
                <Date-picker v-model="formData.time" class="filter-input" format="yyyy/MM/dd"
                             placement="bottom-end" placeholder="选择日期"></Date-picker>
            </FormItem>
            <Button type="primary" icon="ios-search" @click="loadData(1)">搜索</Button>
        </Form>

        <Table stripe border :columns="columns" :data="listData.data"></Table>
        <Spin size="large" fix v-if="load"></Spin>
        <Row v-if="listData.last_page > 1" style="text-align: center;margin-top: 20px">
            <Page @on-change="loadData" :total="listData.total" :page-size="listData.per_page"
                  :current="listData.current_page" size="small"/>
        </Row>
    </div>
</template>

<script>
  import api from './reservationApi';
  import time from '@/js/utils/times';

  export default {
    data() {
      return {
        listData: [],
        formData: {},
        load: false,
        times: [],
        columns: [
          {
            title: '日期',
            align: 'center',
            key: 'date'
          },
          {
            title: '时间范围',
            align: 'center',
            render: (h, params) => {
              let str = [];
              params.row.reservation_time.forEach(value => {
                str.push(h('Button', {
                  props: {
                    size: 'small'
                  },
                  style: {
                    margin: '5px'
                  },
                }, this.times[parseInt(value)].label + ''));
              });
              return h('div', {
                style: {
                  textAlign: 'left'
                },
              }, str);
            }
          },
          {
            title: '状态',
            align: 'center',
            render: (h, params) => {
              let str;
              switch (params.row.status) {
                case 0:
                  str = '待确认';
                  break;
                case 1:
                  str = '成功';
                  break;
                case 2:
                  str = '拒绝';
                  break;
              }
              return h('span', str);
            }
          },
          {
            title: '房间',
            align: 'center',
            render: (h, params) => {
              return h('span', params.row.venue.name);
            }
          },
          {
            title: '操作',
            align: 'center',
            render: (h, params) => {
              return h('Button', {
                props: {
                  size: 'small'
                },
                style: {
                  marginRight: '5px',
                  display: params.row.status === 2 ? 'none' : ''
                },
                on: {
                  click: () => {
                    this.cancel(params.row.id);
                  }
                }
              }, '取消');
            }
          }
        ]
      };
    },
    mounted() {
      this.loadData();
      this.times = time.getTime();
    },
    methods: {
      loadData(page) {
        if (page) {
          this.formData.jump_page = page;
        }
        this.load = true;

        if (this.$formUtil.isStrValid(this.formData.time)) {
          this.formData.date = this.$formUtil.dataToStr(this.formData.time);
        } else {
          this.formData.date = '';
        }
        api.my(this.formData, res => {
          this.load = false;
          this.listData = res.data.data;
        });
      },
      cancel(id) {
        this.$Modal.confirm({
          title: '确定取消预约吗?',
          okText: '确定',
          cancelText: '返回',
          loading: true,
          onOk: () => {
            api.cancel({ id: id }, () => {
                this.$Modal.remove();
                this.$Message.success('取消成功');
                this.loadData();
              },
              () => {
                this.$Modal.remove();
              });
          }
        });
      },
      getTime(times) {
        let str = [];
        times.forEach(value => {
          str += (this.times[parseInt(value)].label + '<br/>');
        });
        return str;
      }
    }
  };
</script>
